import logo from '../../assets/logo192.png';
import NavButton from '../items/NavButton';
import logout from '../../assets/right-from-bracket-solid.svg';
import submit from '../../assets/paper-plane-solid.svg';
import attachments from '../../assets/paperclip-solid.svg';
import ranking from '../../assets/ranking-star-solid.svg';
import { useContext } from 'react';
import { AuthContext } from '../../contexts/authContext';

const NavBar = ({choose}) => {
    const buttons = [
        {
            link:'',
            img:submit,
            alt:'Submit'
        },
        {
            link:'attachments',
            img:attachments,
            alt:'Attachments'
        },
        {
            link:'ranking',
            img:ranking,
            alt:'Ranking'
        }
    ];

    const {authState: {authLoading, isAuthenticated}} = useContext(AuthContext);

    if(authLoading) {
        return null;
    }

    const funcbtn = isAuthenticated ? (
        <>
            <div className='flex-1 flex flex-col justify-center gap-7'>
                {
                    buttons.map(b => (
                        <NavButton key={b.alt} data={b} choose={choose===b.link} />
                    ))
                }
            </div>
            
            <a href='/logout'>
                <div className='group border-r-4 border-r-[#180600]'>
                    <img src={logout} alt='Logout' className='w-1/4 mx-auto off-button group-hover:off-button-hover duration-200' />

                    <div className='text-[#50423E] group-hover:text-[#B2ABAA] duration-200 text-center font-medium text-sm font-mont'>
                        Logout
                    </div>
                </div>
            </a>
        </>
    ) : null;

    return (
        <div className="w-[8%] flex flex-col my-5 justify-center">
            <img src={logo} alt='Logo' className="w-1/2 mx-auto" />
            {funcbtn}
        </div>
    );
}

export default NavBar;